
import {mapActions, mapState, mapMutations} from 'vuex'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

export default {
name:'Content',
data () {
return {
}
},
components: {
ViewBox,
XHeader,
Flexbox,
FlexboxItem,
Sticky
},
created () {
let id = this.$route.params.id
this.getArticle({id: id})
},
methods: {
...mapMutations([
'ADD_COLLECTION',
'REMOVE_COLLECTION'
]),
...mapActions([
'getArticle'
]),
addCollection () {
let id = this.$route.params.id
let index = this.localCollection.findIndex(item => {
return item.id === id
})
if (index === -1) {
this.ADD_COLLECTION({
id,
src: this.article.src,
title: this.article.title
})
this.$Message.success('收藏成功')
} else {
this.REMOVE_COLLECTION(id)
this.$Message.success('删除成功')
}
},
},
computed: {
...mapState([
'article',
'ifReturnState',
'localCollection'
])
},
beforeRouteEnter (to, from, next) {
const id = to.params.id
if (!id) return false
next(vm => {
vm.getArticle({ id })
})
},
}



